.form__button {
  width: 100%;
  margin-right: 5px;
  border: none;
  background-color: teal;
  color: white;
  padding: 10px;
  border-radius: 10px;
}

.form__button:hover {
  background-color: hsl(180, 100%, 30%);
}

.form__button:active {
  background-color: hsl(180, 100%, 35%);
}
